<template>
  <i :class="'fa'+ boldType + ' fa-' + type + ' fa-' + size + 'x'" @click="click"></i>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      default: "home"
    },
    size: {
      type: String,
      default: "1"
    },
    canBold: {
      type: Boolean,
      default: false
    },
    isBold: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return { bold: this.isBold };
  },
  created() {
    if (this.canBold) this.bold = true;
  },
  methods: {
    click() {
      if (this.canBold) {
        this.bold = !this.bold;
        this.$emit("click", !this.bold);
      } else {
        this.$emit("click");
      }
    }
  },
  computed: {
    boldType() {
      if (this.bold) {
        return "r";
      } else {
        return "";
      }
    }
  }
};
</script>

<style scoped>
.fa {
  cursor: pointer;
}
</style>



