<template>
  <BaseIcon class="infinite" :type="type" :size="size" :canBold="canBold" :isBold="isBold" @click="$emit('click')"></BaseIcon>
</template>

<script>
import BaseIcon from "@/components/Icon/BaseIcon";
export default {
  components: { BaseIcon },
  props: {
    type: {
      type: String,
      default: "home"
    },
    size: {
      type: String,
      default: "1"
    },
    canBold: {
      type: Boolean,
      default: false
    },
    isBold: {
      type: Boolean,
      default: false
    }
  }
};
</script>

<style scoped>
@keyframes wink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.infinite {
  animation: wink 2s infinite;
}
</style>


